<template>
  <div :id="`outer-${index}`" class="container-fluid m-0 p-0" v-if="loaded">
    <div
      v-if="mobile"
      :class="[index === 0 ? 'pt-3' : 'pt-2']"
      class="p-3 pb-1 d-flex justify-content-between"
    >
      <div :class="[mobileInfo ? 'strikethrough' : '']" class="home-btn">
        {{ current.primary.project_title[0].text }}
      </div>
      <div v-if="$route.name === ' / Left-Field'">
        ( {{ current.primary.date }} )
      </div>
      <div
        v-if="!mobileInfo && $route.name !== ' / Left-Field'"
        @click="openInfo"
      >
        ( Info )
      </div>
      <div v-if="mobileInfo" @click="mobileInfo = false">( Close )</div>
    </div>
    <div
      :id="`inner-${index}`"
      class="project-content d-flex flex-row mx-0 mb-0"
    >
      <div
        :class="[info || mobileInfo ? 'info-container-open' : '']"
        class="info-container ps-3 d-flex align-items-between"
      >
        <div
          :style="[info || mobileInfo ? 'opacity:1' : '']"
          class="info-wrapper d-flex justify-content-between justify-content-md-end flex-column"
        >
          <div class="works-font d-block d-md-none">
            <div class="works-font d-flex">
              <prismic-rich-text
                style="line-height: 16px; text-transform: uppercase"
                :field="current.primary.client"
              ></prismic-rich-text>
            </div>
            <div>YEAR: {{ current.primary.date }}</div>
          </div>

          <prismic-rich-text
            :field="current.primary.project_description"
            style="text-transform: uppercase"
          ></prismic-rich-text>
          <div class="works-font d-md-flex d-none">
            <prismic-rich-text
              style="line-height: 16px; text-transform: uppercase"
              :field="current.primary.client"
            ></prismic-rich-text>
          </div>
          <prismic-rich-text
            class="works-font pb-0"
            style="text-transform: uppercase"
            :field="current.primary.specifications"
          ></prismic-rich-text>
        </div>
      </div>
      <div
        v-for="(image, index) in current.items"
        :key="index"
        class="col-auto pe-2 image-wrapper position-relative"
      >
        <video
          v-if="image.video.url"
          class="lazyload"
          playsinline
          preload="none"
          muted=""
          data-autoplay=""
          loop
          :src="image.video.url"
        ></video>
        <LazyImage
          v-else
          :index="index"
          :url="image.image.url"
          :height="image.image.dimensions.height"
          :width="image.image.dimensions.width"
        />
      </div>
    </div>
  </div>
</template>
<script setup>
import "lazysizes";
import "lazysizes/plugins/unveilhooks/ls.unveilhooks";
import LazyImage from "@/components/ui/LazyImage.vue";
import { loadImages } from "@/assets/imageLazy";
import { ref, defineProps, toRefs, watch, onMounted } from "vue";

const props = defineProps(["loaded", "info", "current", "mobile", "index"]);
const { loaded, info, current, mobile } = toRefs(props);

const mobileInfo = ref(false);

function openInfo() {
  mobileInfo.value = true;

  document
    .getElementById(`inner-${props.index}`)
    .scroll({ top: 0, left: 0, behavior: "smooth" });

  setTimeout(() => {
    document
      .getElementById(`outer-${props.index}`)
      .scrollIntoView({ block: "start" });
  }, 800);
}
watch(info, () => {
  if (info.value) {
    const projectNav = document.getElementById("project-nav");

    document
      .querySelector(".project-content")
      .scroll({ top: 0, left: 0, behavior: "smooth" });

    setTimeout(() => {
      projectNav.scrollIntoView({ block: "start" });
    }, 800);
  }
});
onMounted(() => {
  loadImages();
});
</script>

