<template>
  <LandingPage :basecolor="landingColour" />
  <NavBar
    :basecolor="landingColour"
    :abouttext="aboutData"
    :email="emailAddress"
    :arena="arena"
    :linkedin="linkedin"
  />

  <router-view v-slot="slotProps">
    <transition name="route" mode="out-in">
      <component :is="slotProps.Component"></component>
    </transition>
  </router-view>

  <PageFooter
    :arena="arena"
    :linkedin="linkedin"
    :email="emailAddress"
    v-if="!onHome"
  />
</template>
<script>
import LandingPage from "@/components/ui/LandingPage.vue";
import NavBar from "@/components/ui/NavBar.vue";
import PageFooter from "@/components/ui/PageFooter.vue";

export default {
  components: {
    LandingPage,
    NavBar,
    PageFooter,
  },
  data() {
    return {
      onHome: true,
      showLanding: true,
      aboutData: {},
      arena: [],
      linkedin: [],
      emailAddress: [],
      colours: [
        "#216649",
        "#212C66",
        "#82290C",
        "#623B81",
        "#60361E",
        "#1F1F1B",
        "#182E24",
        "#B39884",
      ],
    };
  },
  methods: {
    getContent() {
      this.$prismic.client.getSingle("bio__navigation").then((response) => {
        this.aboutData = {
          partOne: response.data.biography,
          partTwo: response.data.address,
        };
        this.emailAddress = response.data.email_address;
        this.arena = response.data.arena;
        this.linkedin = response.data.linkedin;
     
      });
    },
    showFooter() {
      const path = this.$route.path;

      if (path.length <= 1) {
        this.onHome = true;
      } else {
        this.onHome = false;
      }
    },
  },
  created() {
    this.getContent();
    this.showFooter();
  },
  computed: {
    landingColour() {
      const index = Math.floor(Math.random() * this.colours.length);
      return this.colours[index];
    },
  },
  watch: {
    $route() {
      this.showFooter();
    },
  },
};
</script>


