<template>
  <div id="project-nav" class="p-3">
    <div class="row d-flex align-items-start justify-content-between">
      <div class="col-md-4 col-12 order-1 order-md-2">
        <span
          @mouseover="previewProjectEnter(project)"
          @mouseleave="previewProjectLeave"
          class="index-nav"
          @click="changeProject(project)"
          v-for="(project, index) in projects"
          :key="index"
          >{{ index + 1 }}
          <span v-if="index + 1 < projects.length">/ </span></span
        >
      </div>

      <div
        v-if="loaded && !hoverPreview"
        class="col-md-6 col-12 home-btn order-2 order-md-1"
      >
        {{ title }} /
        {{ date }}
      </div>
      <div
        v-if="loaded && hoverPreview"
        class="col-md-6 col-12 home-btn order-2 order-md-1"
      >
        {{ currentTitle }} /
        {{ currentDate }}
      </div>

      <div
        @click="toggleInfo"
        :class="[showInfo ? 'strikethrough' : '']"
        class="col-1 p-0 d-none d-md-block info-btn order-md-3"
      >
        Information
      </div>
      <div class="col-12"></div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["loaded", "title", "date", "projects"],
  data() {
    return {
      showInfo: false,
      currentTitle: "",
      currentDate: "",
      hoverPreview: false,
    };
  },
  methods: {
    changeProject(e) {
      this.showInfo = false;
      this.$emit("info", this.showInfo);
      this.$emit("changeproject", e);
    },
    toggleInfo() {
      this.showInfo = !this.showInfo;
      this.$emit("info", this.showInfo);
    },
    previewProjectEnter(el) {
      this.currentTitle = el.primary.project_title[0].text;
      this.currentDate = el.primary.date;
      this.hoverPreview = true;
    },
    previewProjectLeave() {
      this.hoverPreview = false;
    },
  },
};
</script>

<style scoped>
.index-nav {
  user-select: none;
  opacity: 0.2;
}
@media only screen and (min-width: 768px) {
  .index-nav {
    opacity: 1;
  }
}

.index-nav:hover {
  cursor: pointer;
}
.strikethrough {
  text-decoration: line-through;
}
.info-btn {
  user-select: none;
}
.info-btn:hover {
  cursor: pointer;
}
</style>